import React, { Fragment, useState } from 'react';

import differenceInDays from 'date-fns/difference_in_days';
import addDays from 'date-fns/add_days';
import subDays from 'date-fns/sub_days';
import isWithinRange from 'date-fns/is_within_range';

import { determineAlreadySelectedTripDates } from '../helpers/dateUtils';

import Spinner from './Spinner.react';
import DateRange from './DateRange.react';
import LocationMap from './LocationMap.react';
import TripCard from './TripCard.react';
import NewTrip from './NewTrip.react';

const TripList = ({ user, activeUser, locations, isFetching }) => {
  const [dateSelection, setDateSlider] = useState(new Date());
  const [tripSelection, setSelectedTrip] = useState(null);
  const [dateSpreadOverride] = useState(null);

  // TODO: Allow overriding the date spread
  // const [dateSpreadOverride, setDateSpread] = useState(null);

  if (!locations || isFetching) {
    return <Spinner />;
  }
  if (locations.length === 0) {
    return (
      <div>
        No trips yet...
        <br />
        {user.id === activeUser && (
          <Fragment>
            <hr />
            <h4>ADD NEW TRIP</h4>
            <NewTrip trips={locations} />
          </Fragment>
        )}
      </div>
    );
  }

  const dateSpread =
    dateSpreadOverride ||
    Math.max(Math.floor(getDaysInRange(locations) / 4), 10);
  const disabledDates = determineAlreadySelectedTripDates(locations);

  // Only show locations +x days from selected date
  const filteredLocations = locations.filter(
    isWithinDaysN(dateSelection, dateSpread)
  );

  return (
    <Fragment>
      <LocationMap locations={filteredLocations} onSelect={setSelectedTrip} />
      <input
        className="uk-range"
        type="range"
        onChange={e =>
          setDateSlider(dateFromPercentage(e.target.value, locations))
        }
        value={percentageFromDate(dateSelection, locations)}
      />
      <label style={{ margin: 'auto' }}>
        <DateRange
          className="uk-flex-around"
          hideDaysCount
          start_date={dateSelection}
          plusNum={dateSpread}
        />
      </label>

      {user.id === activeUser && (
        <Fragment>
          <hr />
          <h4>ADD NEW TRIP</h4>
          <NewTrip trips={locations} />
        </Fragment>
      )}

      <hr />

      {filteredLocations.map(trip => (
        <TripCard
          key={trip.id}
          disabledDates={disabledDates}
          {...trip}
          isHighlighted={trip.id === tripSelection}
          viewOnly={user.id !== activeUser}
        />
      ))}
    </Fragment>
  );
};

const getDaysInRange = locations => {
  if (locations.length === 0) {
    return 0;
  }
  const diff = differenceInDays(
    locations[locations.length - 1].end_date,
    locations[0].start_date
  );
  return Math.abs(diff);
};

const percentageFromDate = (date, locations) => {
  const daysInRange = getDaysInRange(locations);
  const daysFromStart = differenceInDays(date, locations[0].start_date);
  return Math.round((daysFromStart / daysInRange) * 100);
};

const dateFromPercentage = (percentage, locations) => {
  const daysInRange = getDaysInRange(locations);
  const daysFromStart = Math.round(daysInRange * (percentage / 100));
  return addDays(locations[0].start_date, daysFromStart);
};

const isWithinDaysN = (dateSelection, dateSpread) => ({
  start_date,
  end_date
}) => {
  return (
    isWithinRange(
      start_date,
      dateSelection,
      addDays(dateSelection, dateSpread)
    ) ||
    isWithinRange(end_date, dateSelection, addDays(dateSelection, dateSpread))
  );
};

export default TripList;
